"use strict";

var ethereumUtil = require("ethereumjs-util");

var packet = require("dns-packet");

var sha1 = require("js-sha1");

function getHeader(key) {
  return packet.dnskey.encode(key.data).slice(2);
}

function checkDigest(anchor, name, input, digestType) {
  var digest = void 0;

  switch (digestType) {
    case 1:
      digest = new Buffer(sha1.arrayBuffer(Buffer.concat([packet.name.encode(name), input])));
      return anchor.data.digest.equals(digest);

    case 2:
      digest = ethereumUtil.sha256(Buffer.concat([packet.name.encode(name), input]));
      return anchor.data.digest.equals(digest);

    case 253:
      // this is dummy so always returns true
      return true;

    default:
      throw "DIGEST " + digestType + " NOT SUPPORTED";
  }
}

function getKeyTag(input) {
  var keytag = 0;

  for (var i = 0; i < input.length; i++) {
    var v = input[i];

    if (i & 1 != 0) {
      keytag += v;
    } else {
      keytag += v << 8;
    }
  }

  keytag += keytag >> 16 & 0xffff;
  keytag &= 0xffff;
  return keytag;
}

module.exports = {
  getHeader: getHeader,
  getKeyTag: getKeyTag,
  checkDigest: checkDigest
};